@import url(https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Kaisei+HarunoUmi&family=Playfair+Display&family=STIX+Two+Math&display=swap);
html,
body {
  font-size: 16px;
  font-family: 'STIX Two Math', serif, 'Playfair Display', serif;
  scroll-behavior: smooth;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
