	line-height: 2.25rem;
}
.text-8xl {
	font-size: 6rem;
	line-height: 1;
}
.font-bold {
	font-weight: 700;
}
.font-medium {
	font-weight: 500;
}
.capitalize {
	text-transform: capitalize;
}
.text-yellow-400 {
	--tw-text-opacity: 1;
	color: rgba(250, 204, 21, var(--tw-text-opacity));
}
.text-white {
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.text-indigo-500 {
	--tw-text-opacity: 1;
	color: rgba(99, 102, 241, var(--tw-text-opacity));
}
.text-black {
	--tw-text-opacity: 1;
	color: rgba(0, 0, 0, var(--tw-text-opacity));
}
.text-gray-400 {
	--tw-text-opacity: 1;
	color: rgba(161, 161, 170, var(--tw-text-opacity));
}
.underline {
	text-decoration: underline;
}
.ring-8 {
	--tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
	--tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(8px + var(--tw-ring-offset-width)) var(--tw-ring-color);
	box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}
.filter {
	filter: var(--tw-filter);
}
.duration-300 {
	transition-duration: 300ms;
}
.ease-linear {
	transition-timing-function: linear;
}
.hover\:scale-105:hover {
	--tw-scale-x: 1.05;
	--tw-scale-y: 1.05;
	transform: var(--tw-transform);
}
.hover\:text-white:hover {
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.hover\:underline:hover {
	text-decoration: underline;
}
.hover\:opacity-90:hover {
	opacity: 0.9;
}
@media (min-width: 640px) {

	.sm\:hidden {
		display: none;
	}

	.sm\:text-lg {
		font-size: 1.125rem;
		line-height: 1.75rem;
	}

	.sm\:text-sm {
		font-size: 0.875rem;
		line-height: 1.25rem;
	}
}
@media (min-width: 768px) {

	.md\:hidden {
		display: none;
	}

	.md\:text-xl {
		font-size: 1.25rem;
		line-height: 1.75rem;
	}

	.md\:text-lg {
		font-size: 1.125rem;
		line-height: 1.75rem;
	}
}
@media (min-width: 1024px) {

	.lg\:block {
		display: block;
	}

	.lg\:text-2xl {
		font-size: 1.5rem;
		line-height: 2rem;
	}

	.lg\:text-xl {
		font-size: 1.25rem;
		line-height: 1.75rem;
	}
}
@media (min-width: 1280px) {

	.xl\:block {
		display: block;
	}

	.xl\:text-2xl {
		font-size: 1.5rem;
		line-height: 2rem;
	}

	.xl\:text-xl {
		font-size: 1.25rem;
		line-height: 1.75rem;
	}
}

